
import { mapGetters, mapActions } from 'vuex';
import formItem from '@/component/form-item.vue';
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
components: {
formItem,
Datetime,
PopupPicker
},
name: 'InitiateRights',
data() {
return {
routate1: false,
submitForm: false,
visibility: false,
showPopupPicker: false,
mapValue: '',
mapObj: '',
loctionName: '',
textNull: '',
formData: {
title: '',
infringer: '',
units: '',
unitsDate: '',
unitsTel: '',
type: [],
selfTel: '',
remark: '',
location: ''
},
rlues: {
title: {
focus: false,
blur: true
},
infringer: {
focus: false,
blur: true
},
units: {
focus: false,
blur: true
},
unitsDate: {
focus: false,
blur: true
},
unitsTel: {
focus: false,
blur: true
},
type: {
focus: false,
blur: true
},
selfTel: {
focus: false,
blur: true
},
remark: {
focus: false,
blur: true
},
location: {
focus: false,
blur: true
}
},
list1: [['发票', '领导签字', '文书', '等等等等']]
};
},
methods: {
...mapActions([]),
getDate() {
this.visibility = true;
},
getFocus(info) {
this.rlues[info].focus = true;
this.rlues[info].blur = false;
},
getBlur(info) {
this.rlues[info].focus = false;
this.rlues[info].blur = true;
},
inputEmpty(info) {
this.formData[info] = '';
},
submitFormBtn() {
this.submitForm = true;
},
typeSelect() {
this.showPopupPicker = true;
}
},
watch: {},
computed: {
...mapGetters([])
},
created() {},
mounted() {
let elem = document.querySelector('#vux_view_box_body');
setTimeout(() => {
elem.style.paddingBottom = '0';
}, 10);
let self = this;
self.mapObj = new AMap.Map('iCenter');
self.mapObj.plugin('AMap.Geolocation', function() {
self.geolocation = new AMap.Geolocation({
enableHighAccuracy: true,
timeout: 3000,
maximumAge: 0,
convert: true,
showButton: true,
buttonPosition: 'RB',
buttonOffset: new AMap.Pixel(10, 20),
showMarker: true,
showCircle: true,
panToLocation: true,
zoomToAccuracy: true,
useNative: true
});
self.mapObj.addControl(self.geolocation);
self.geolocation.getCurrentPosition();
AMap.event.addListener(self.geolocation, 'complete', onComplete);
AMap.event.addListener(self.geolocation, 'error', onError);

});
function onError(data) {

console.log(data);
}

function onComplete(data) {
console.log(data);
self.formData.location = data.formattedAddress;
}
}
};
